html {
  font-size: 80%;
}

@media (min-width: 480px) {
  html {
    font-size: 100%;
  }
}

body {
  min-width: 320px;
  background-color: #111;
  margin: 0;
  font-family: Jost, sans-serif;
}

h1, h2 {
  margin: 0;
  font-weight: normal;
}

.App {
  text-align: center;
}

.App-logo {
  height: 100%;
  animation: 20s linear infinite App-logo-spin;
}

.App-header {
  height: 80px;
  color: #fff;
  background-color: #000;
  padding: 20px;
}

.App-header h1 {
  background-image: linear-gradient(120deg, #155799 50%, #991557);
  background-position: 100%;
  background-size: 200% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 3rem;
  line-height: 3rem;
  display: inline-block;
}

.App-header h2 {
  color: #ddd;
  margin-top: .5em;
  font-size: 1rem;
}

.App-footer {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0;
}

.App-indicator {
  color: #fff;
  background-color: #111;
  border-radius: 4px 4px 0 0;
  justify-content: space-between;
  align-items: center;
  padding: 6px 15px;
  display: inline-flex;
}

.App-button {
  width: 32px;
  height: 32px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  vertical-align: middle;
  background: none;
  border: none;
  border-radius: 50%;
  outline: none;
  padding: 0;
  font-size: .75rem;
}

.App-buttonIcon {
  fill: currentColor;
  height: 100%;
  display: block;
}

.App-wrapper {
  height: calc(100vh - 180px);
  justify-content: center;
  align-items: center;
  margin: 15px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.App-zoom {
  width: 100%;
  height: 100%;
  display: block;
}

.App-image {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.App-card {
  width: 360px;
  text-align: left;
  color: #fff;
  background-color: #000000b3;
  padding: 1.5em;
  font-weight: 300;
  display: none;
  position: absolute;
  bottom: 30px;
  right: 30px;
}

.App-cardHeader {
  margin-bottom: 1em;
}

.App-card h3 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 400;
}

.App-card p {
  margin: 0 0 1em;
}

.App-card a, .App-card a:visited {
  color: #61dafb;
}

.App-zoomLabel {
  width: 60px;
  vertical-align: middle;
  display: inline-block;
}

@media (min-width: 768px) {
  .App-wrapper {
    margin: 30px;
  }

  .App-card {
    display: block;
  }
}

